.content {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient( 45deg, $color-pickled-bluewood, $color-wedgewood 50%, $color-blue-bayoux ); }

%frame {
  padding: 3rem;
  transition: 0.2s;
  position: fixed;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  top: 0;
  bottom: 0;
  padding-bottom: 7rem;
  right: -100%; }

.hired {
  @extend %frame; }

.who-made-it {
  @extend %frame; }

.opportunities {
  @extend %frame; }

.zero-to-one {
  @extend %frame; }

.one-to-hero {
  @extend %frame; }

.chat {
  @extend %frame; }

.faq {
  @extend %frame; }

.content-toggle {
  display: none; }

.content-toggle:checked + .hired,
.content-toggle:checked + .who-made-it,
.content-toggle:checked + .opportunities,
.content-toggle:checked + .zero-to-one,
.content-toggle:checked + .one-to-hero,
.content-toggle:checked + .chat,
.content-toggle:checked + .faq {
  right: 0; }

